
import { Helmet } from "react-helmet";
import { send } from 'emailjs-com';
import { useHistory } from "react-router-dom";
import React, { useState, useEffect } from 'react';
function Signup() {
    let history = useHistory();
    const [toSend, setToSend] = useState({
    });
    const handleChange = (e) => {
    setToSend({ ...toSend, [e.target.name]: e.target.value });
    };
    const onSubmit = (e) => {
        e.preventDefault();
        
        let msg = {message : JSON.stringify(toSend)}
        console.log(msg)
        send(
        'service_81qhnac',
        'template_pwdbk4v',
        msg,
        'user_FNM2N5TLP9QWEBN2rYAQt'
        )
        .then((response) => {
            if (response.status == "200"){
                console.log('SUCCESS!', response.status, response.text);
            }
            
        })
        .catch((err) => {
            console.log('FAILED...', err);
        });
        history.push("/signup-confirmation")

    };
    const css = `
        .App {
            background: url('https://calirextn.com/assets/images/loading-background.svg');
            background-position: top center;
            background-size: cover;
            background-repeat: no-repeat;
        }
    `
    return (
        <div className= "SignupForm">
            <Helmet>
                <style>{css}</style>
            </Helmet>
            <div className="auth-wrapper p-5">
                <div className="auth-inner"> 
                    <form onSubmit={onSubmit}>
                        <h3>S&#39;enregistrer</h3>
                        <div className="form-group">
                            <label>Societé</label>
                            <input type="text" className="form-control" placeholder="Societé" required 
                            defaultValue={toSend.company}
                            name='Societé'
                            onChange={handleChange}
                            />
                        </div>
                        <div className="form-group">
                            <label>Prénom</label>
                            <input type="text" className="form-control" placeholder="EX: Prénom" required 
                            defaultValue={toSend.firstname}
                            name='Prénom'
                            onChange={handleChange}
                            />
                        </div>

                        <div className="form-group">
                            <label>Nom</label>
                            <input type="text" className="form-control" placeholder="EX: Nom" required 
                            defaultValue={toSend.lastname}
                            name='Nom'
                            onChange={handleChange}
                            />
                        </div>
                        <div className="form-group">
                            <label>Numéro de téléphone</label>
                            <input type="text" className="form-control" placeholder="EX: 22111333" required 
                            defaultValue={toSend.phone}
                            name='Numéro de téléphone'
                            onChange={handleChange}
                            />
                        </div>
                        <div className="form-group">
                            <label>Adresse</label>
                            <input type="text" className="form-control" placeholder="EX: 4 avenue habib bourguiba" required 
                            defaultValue={toSend.address}
                            name='Adresse'
                            onChange={handleChange}
                            />
                        </div>
                        <div className="form-group">
                            <label>Ville</label>
                            <input type="text" className="form-control" placeholder="EX: Beb Saadoun" required 
                            defaultValue={toSend.town}
                            name='Ville'
                            onChange={handleChange}
                            />
                        </div>
                        <div className="form-group">
                            <label>Code postal</label>
                            <input type="text" className="form-control" placeholder="EX: 1000" required 
                            defaultValue={toSend.postalcode}
                            name='Code postal'
                            onChange={handleChange}
                            />
                        </div>
                        <div className="form-group">
                            <label>État</label>
                            <input type="text" className="form-control" placeholder="EX: Tunis" required 
                            defaultValue={toSend.state}
                            name='État'
                            onChange={handleChange}
                            />
                        </div>

                        <div className="form-group">
                            <label>Adresse email</label>
                            <input type="email" className="form-control" placeholder="EX: client@societe.com" 
                            name='Adresse email'
                            defaultValue={toSend.email}
                            onChange={handleChange}
                            />
                        </div>

                        <div className="form-group">
                            <label>Mot de passe</label>
                            <input type="password" className="form-control" placeholder="EX: Mot de passe" 
                            defaultValue={toSend.password}
                            name='Mot de passe'
                            onChange={handleChange}
                            />
                        </div>

                        <button type="submit" className="btn btn-primary btn-block">Envoyer</button>
                        <p className="forgot-password text-right">
                            Déjà client? <a href="/login">se connecter?</a>
                        </p>
                    </form>
                </div>
            </div>
        </div>
    );
}

export default Signup;
